import { Toast } from 'vant'

class validForm {
    constructor() {
        this.validUserFormReg = {
            nickName: {
                reg: /^[\u4e00-\u9fa5\w]{1,10}$/,
                msg: '用户名格式不正确'
            },
            phone: {
                reg: /^1[3-9]\d{9}$/,
                msg: '手机号码格式不正确'
            },
            password: {
                reg: /^[a-zA-Z]\w{5,15}$/,
                msg: '密码格式不正确且以字母开头'
            }
        }
    }

    // 验证表单
    validUserForm(obj) {
        for (var key in obj) {
            if (!this.validUserFormReg[key].reg.test(obj[key])) {
                Toast(this.validUserFormReg[key].msg)
                return false;
            }
            return true;
        }

    }
}


export default new validForm()