<template>
  <div>
    <div class="login">
      <van-nav-bar
        title="登录"
        left-text="注册"
        right-text="关闭"
        @click-left="goPage('Register')"
        @click-right="goPage('Home')"
      />
      <div class="logo">
        <img class="auto-img" src="../assets/images/logo.png" alt />
      </div>

      <van-form @submit="login">
        <van-field
          maxlength="11"
          type="text"
          class="login-bg"
          left-icon="contact"
          placeholder="请输入手机号"
          v-model="userInfo.phone"
        />

        <van-field
          :type="isEye?'password':'text'"
          maxlength="12"
          class="login-bg"
          left-icon="lock"
          :right-icon="isEye?'closed-eye':'eye-o'"
          @click-right-icon="viewPassword"
          placeholder="请输入密码"
          v-model="userInfo.password"
        />

        <p>
          <a href="#" target="_blank" rel="noopener noreferrer">忘记密码</a>
        </p>

        <van-button class="login-bg">登录</van-button>
      </van-form>
    </div>
  </div>
</template>

<script>
import validForm from "../assets/js/validForm";
export default {
  data() {
    return {
      //用户注册信息
      userInfo: {
        phone: "",
        password: ""
      },

      // 是否看见密码
      isEye: true
    };
  },

  methods: {
    goPage(name) {
      this.$router.push({ name });
    },

    // 是否显示密码
    viewPassword() {
      if (this.isEye) {
        this.isEye = !this.isEye;
      } else {
        this.isEye = true;
      }
      
    },

    // 登录操作
    //登录
    login() {
      //进行验证表单
      if (validForm.validUserForm(this.userInfo)) {
        //复制对象
        let data = Object.assign({}, this.userInfo);

        //请求密钥
        data.appkey = this.appkey;

        //开启加载提示
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,

          //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
          duration: 0
        });

        //发起登录请求
        this.axios({
          method: "POST",
          url: "/login",

          //post传递参数需要写在data
          data
        })
          .then(result => {
            //关闭加载提示
            this.$toast.clear();
            // 
            if (result.data.code == 200) {
              //跳转到菜单
              this.$router.push({ name: "Recommend" });

              //存储token，用于后面登录验证
              localStorage.setItem("__Tk", result.data.token);
            } else {
              this.$toast(result.data.msg);
            }
          })
          .catch(err => {
            //关闭加载提示
            this.$toast.clear();
            
          });
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import "../assets/iconfont/font/iconfont.css";

.login {
  margin: 0 auto;
  .logo {
    text-align: center;
    img {
      width: 200px;
    }
  }
  p {
    text-align: right;
    a {
      color: #cecece;
    }
  }
  .login-bg {
    background-color: #36200c;
    color: #fff;
    width: 100%;
    margin: 10px auto;
  }

  .van-form {
    width: 90%;
    margin: 0 auto;
  }

  .other-login {
    .login-item {
      float: left;
      width: ceil(100%/2);
      text-align: center;
      span {
        font-size: 48px;
        color: #36200c;
      }
    }
  }
}
// 深度修改样式
/deep/ .van-nav-bar__text {
  color: #36200c;
}
/deep/ .van-field__control {
  color: #fff;
}

/deep/ .van-nav-bar {
  background-color: #36200c;
}
/deep/.van-nav-bar__title {
  color: #fff;
}
/deep/ .van-nav-bar__text {
  color: #fff;
}
</style>